import React, { useState } from 'react';
import {
  Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, IconButton, Collapse, Box
} from '@mui/material';
import { KeyboardArrowDown, KeyboardArrowUp } from '@mui/icons-material';

const data = [
  {
    applicationName: 'App 1',
    totalDownload: '100 MB',
    totalUploaded: '50 MB',
    downloadSpeed: '10 MB/s',
    uploadSpeed: '5 MB/s',
    children: [
      {
        applicationName: 'Sub App 1.1',
        totalDownload: '50 MB',
        totalUploaded: '25 MB',
        downloadSpeed: '5 MB/s',
        uploadSpeed: '2.5 MB/s',
      },
      {
        applicationName: 'Sub App 1.2',
        totalDownload: '50 MB',
        totalUploaded: '25 MB',
        downloadSpeed: '5 MB/s',
        uploadSpeed: '2.5 MB/s',
      },
    ],
  },
  {
    applicationName: 'App 2',
    totalDownload: '200 MB',
    totalUploaded: '100 MB',
    downloadSpeed: '20 MB/s',
    uploadSpeed: '10 MB/s',
    children: [
      {
        applicationName: 'Sub App 2.1',
        totalDownload: '100 MB',
        totalUploaded: '50 MB',
        downloadSpeed: '10 MB/s',
        uploadSpeed: '5 MB/s',
      },
    ],
  },
];

function Row({ row }) {
  const [open, setOpen] = useState(false);

  return (
    <>
      <TableRow>
        <TableCell>
          {row.children && (
            <IconButton aria-label="expand row" size="small" onClick={() => setOpen(!open)}>
              {open ? <KeyboardArrowUp /> : <KeyboardArrowDown />}
            </IconButton>
          )}
        </TableCell>
        <TableCell>{row.applicationName}</TableCell>
        <TableCell>{row.totalDownload}</TableCell>
        <TableCell>{row.totalUploaded}</TableCell>
        <TableCell>{row.downloadSpeed}</TableCell>
        <TableCell>{row.uploadSpeed}</TableCell>
      </TableRow>
      {row.children && (
        <TableRow>
          <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
            <Collapse in={open} timeout="auto" unmountOnExit>
              <Box margin={1}>
                <Table size="small" aria-label="purchases">
                  <TableHead>
                    <TableRow>
                      <TableCell />
                      <TableCell>Application Name</TableCell>
                      <TableCell>Total Download</TableCell>
                      <TableCell>Total Uploaded</TableCell>
                      <TableCell>Download Speed</TableCell>
                      <TableCell>Upload Speed</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {row.children.map((childRow, index) => (
                      <Row key={index} row={childRow} />
                    ))}
                  </TableBody>
                </Table>
              </Box>
            </Collapse>
          </TableCell>
        </TableRow>
      )}
    </>
  );
}

function TreeTable() {
  return (
    <TableContainer component={Paper}>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell />
            <TableCell>Application Name</TableCell>
            <TableCell>Total Download</TableCell>
            <TableCell>Total Uploaded</TableCell>
            <TableCell>Download Speed</TableCell>
            <TableCell>Upload Speed</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {data.map((row, index) => (
            <Row key={index} row={row} />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

export default TreeTable;